@mixin flex-container($justify-content, $direction) {
  display: flex;
  align-items: center;
  justify-content: $justify-content;
  flex-direction: $direction; }

.flex {
  @include flex-container(center, row); }

.flexBtn {
  @include flex-container(center, row);
  margin: 0;
  padding: 0; }

.divBtn {
  margin: 0;
  padding: 5px; }

.divInfoRating {
  margin: 0;
  padding: 0px 0px 10px 5px; }

.divInfoRank {
  margin: 0;
  padding: 2px 0px 5px 5px; }
a {
  user-select: none; }

.rankCell {
  margin: 0;
  padding: 0; }




.flexStart {
  display: flex;
  align-items: start;
  justify-content: start;
  flex-direction: column;
  padding: 0; }

.divPadding {
  padding: 0 0 0 12px; }

.divPaddingLeft {
  padding: 0 0 0 12px; }

.linkBuy {
