.card {
  display: flex;
  align-items: center;
  justify-content: start;
  flex-direction: row;
  box-shadow: 0px 0px 10px 0 rgba(0,0,0,0.3);
  transition: 0.3s;
  margin: 10px 5px 10px 5px;
  background-color: white;
  border-radius: 10px;
  border: 1px;
  border-style: solid;
  border-color: white;
  padding: 5px; }

.cardShop {
  display: flex;
  align-items: start;
  justify-content: start;
  flex-direction: column;
  box-shadow: 0px 0px 10px 0 rgba(0,0,0,0.3);
  transition: 0.3s;
  margin: 10px 5px 10px 15px;
  background-color: white;
  border-radius: 10px;
  border: 1px;
  border-style: solid;
  border-color: white;
  padding: 5px;
  width: 90%; }


.cardShopInventory {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  box-shadow: 0px 0px 10px 0 rgba(0,0,0,0.3);
  transition: 0.3s;
  margin: 10px 5px 10px 15px;
  background-color: white;
  border-radius: 10px;
  border: 1px;
  border-style: solid;
  border-color: white;
  padding: 5px;
  width: 100%; }

